#facebook, #linkedin, #gmail, #whatsapp {
	padding:1px;
   border:1px solid #021a40;
   background-color:#000;
}

#facebook:hover, #linkedin:hover, #gmail:hover, #whatsapp:hover {
	cursor: pointer;
	-webkit-animation: flipping 2s linear 1;
	        animation: flipping 2s linear 1;
}

@-webkit-keyframes flipping {
	0% {-webkit-transform: rotateY(0) rotateX(0) rotateZ(0);transform: rotateY(0) rotateX(0) rotateZ(0)}
	100% {-webkit-transform: rotateY(360deg) rotateX(360deg) rotateZ(360deg);transform: rotateY(360deg) rotateX(360deg) rotateZ(360deg)}
}

@keyframes flipping {
	0% {-webkit-transform: rotateY(0) rotateX(0) rotateZ(0);transform: rotateY(0) rotateX(0) rotateZ(0)}
	100% {-webkit-transform: rotateY(360deg) rotateX(360deg) rotateZ(360deg);transform: rotateY(360deg) rotateX(360deg) rotateZ(360deg)}
}


