.contact {
	padding: 0px;
    margin: 0px;
	padding-left: 12%;
	padding-right: 12%;
	padding-bottom: 5%;
	font-size: 2vw;
	background-color: #9C0D38;
	border-bottom-right-radius: 30%;
	border-bottom-left-radius: 30%;	
}

.container {
	text-align: center;
}

.soc_med {
	display: inline-block;
	width: 50px;
	height: 50px;
	margin: 5px;
}

#facebook, #linkedin, #gmail, #whatsapp {
	padding:1px;
   border:1px solid #021a40;
   background-color:#000;
}

#facebook:hover, #linkedin:hover, #gmail:hover, #whatsapp:hover {
	cursor: pointer;
	animation: flipping 2s linear 1;
}

@keyframes flipping {
	0% {transform: rotateY(0) rotateX(0) rotateZ(0)}
	100% {transform: rotateY(360deg) rotateX(360deg) rotateZ(360deg)}
}


